body{font-family:Raleway,sans-serif;font-size:20px;height:100vh;margin:0;min-height:100vh;overflow:hidden;padding:0}.preloader{-ms-flex-align:center;-ms-flex-direction:column;-ms-flex-pack:center;-webkit-box-align:center;-webkit-box-direction:normal;-webkit-box-orient:vertical;-webkit-box-pack:center;align-items:center;background:rgba(0,0,0,.85);color:hsla(0,0%,100%,.5);display:-webkit-box;display:-ms-flexbox;display:flex;flex-direction:column;height:100vh;justify-content:center;position:absolute;width:100%;z-index:15}.preloader .spinner{margin:100px auto 0;text-align:center;width:70px}.preloader .spinner>div{-webkit-animation:sk-bouncedelay 1.4s infinite ease-in-out both;animation:sk-bouncedelay 1.4s infinite ease-in-out both;background-color:#f2af00;border-radius:100%;display:inline-block;height:18px;width:18px}.preloader .spinner .bounce1{-webkit-animation-delay:-.32s;animation-delay:-.32s}.preloader .spinner .bounce2{-webkit-animation-delay:-.16s;animation-delay:-.16s}@-webkit-keyframes sk-bouncedelay{0%,80%,to{-webkit-transform:scale(0)}40%{-webkit-transform:scale(1)}}@keyframes sk-bouncedelay{0%,80%,to{-webkit-transform:scale(0);transform:scale(0)}40%{-webkit-transform:scale(1);transform:scale(1)}}header{-ms-flex-align:center;-ms-flex-pack:center;-webkit-box-align:center;-webkit-box-pack:center;-webkit-box-shadow:0 2px 10px rgba(0,0,0,.3);align-items:center;background:#b7295a;box-shadow:0 2px 10px rgba(0,0,0,.3);color:#fff;display:-webkit-box;display:-ms-flexbox;display:flex;font-family:Playfair Display,cursive;font-size:2rem;font-style:italic;font-weight:900;height:3.5rem;justify-content:center;left:0;position:absolute;right:0;text-shadow:0 0 5px rgba(0,0,0,.5);top:0;z-index:9}header span{display:inline-block;font-weight:400;margin:0 .5rem}main,main .selection{display:-webkit-box;display:-ms-flexbox;display:flex;height:100%;overflow:hidden}main .selection{-ms-flex:1 0 auto;-ms-flex-align:center;-ms-flex-pack:center;-webkit-box-align:center;-webkit-box-flex:1;