er .main-carousel{height:100%;width:100%}.photoViewer .main-carousel .flickity-viewport{height:100%!important}.photoViewer .carousel-cell{background-position:50%!important;background-repeat:no-repeat!important;background-size:cover!important;height:100%;text-align:center;width:100%}.photoViewer .flickity-button{opacity:.3}